import { primaryColor } from '../../../../constants/styles'
import { overridePrimary, framed, overridePerms } from '../../../../constants/partner'
import { getColorForValueForImpPts, getColorForValueForOU, getColorForValueForGPPValue } from '../../../../utils/color'

const StyledTable = styled(Table)`
  td {
    text-align: center;
    padding: 2px !important;
  }

  tr th {
    text-align: center !important;
  }

  tr:first-child {
    th {
      color: white !important;
      background: ${framed ? overridePrimary : primaryColor} !important;
    }
  }

  tr.ant-table-row-level-0:hover > td {
    background: #e6f7ff !important;
  }

  *, *:before, *:after {
    -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;
  }

  .divide {
    border-right: 2px solid ${framed ? overridePrimary : primaryColor} !important;
  }
`
const PlayerText = styled.span`
  cursor: pointer;
  font-weight: 600;
  color: #1d3557;
  &:hover {
    color: #1890ff;
  }
`

const Lock = () => (
  <Tooltip title="Upgrade for this feature">
    <LockOutlined />
  </Tooltip>
)

const ArrowInput = styled.div`
  margin: -5px 0px -5px 0px;
`

const UP_COLORS = ['#4BD031', '#42CF26', '#39CE1B','#2ECF0E', '#23CF01']
const UpArrow = styled(CaretUpOutlined)`
  color: ${(props) => {
    const val = Number(props.value)
    if (val < 1) return '#999999 !important'
    else return `${UP_COLORS[val - 1]} !important`
  }};
  cursor: pointer;
  z-index: 2;
  padding: 5px;
`

const DOWN_COLORS = ['#D25F5F', '#D14444', '#D02F2F','#D01818', '#CF0101']
const DownArrow = styled(CaretDownOutlined)`
  color: ${(props) => {
    const val = Number(props.value)
    if (val > -1) return '#999999 !important'
    else return `${DOWN_COLORS[(val * -1) - 1]} !important`
  }};
  cursor: pointer;
  z-index: 2;
  padding: 5px;
`

const BoostText = styled.div`
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: -10px;
  z-index: 0;
  font-size: 13px;
`

